<template>
  <div class="clock card m-3">
    <div class="card-header">
      <h5>{{ $t('applications') }}</h5>
    </div>
    <div class="card-body">
      <a v-for="application in applications" :key="application"
      :href="application.url" class="btn btn-primary m-2 btn-lg">
        {{ application.name[$i18n.locale] }}
      </a>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Applications',
  data() {
    return {
      applications: [],
    };
  },
  created() {
    fetch('/json/applications.json')
      .then((r) => r.json())
      .then((json) => {
        this.applications = json.Applications;
      });
  },
};
</script>
